<template> 
	<el-card v-theme:position="'wide'">
		<h1>博客总览</h1>
		<el-row>
			<el-col :span="12" :offset="12">
				<el-input placeholder="搜索Blog" suffix-icon="el-icon-search" v-model="search" class="search"></el-input>
			</el-col>
		</el-row>
			<el-card class="box-card" v-for="(blog,index) in filteredBlogs" :key="index"  shadow="hover">
					<router-link v-bind:to="'/blog/' + blog.id"><h3 v-rainbow>{{blog.title}}</h3></router-link>
					<div class="text item">
						{{blog.content | snippet}}
					</div>
			</el-card>
	</el-card>
</el-container>

</template>

<script>

export default {
  name: 'show-blogs',
  data(){
  	return {
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	this.$http.get('https://myblog-30cdf.firebaseio.com/posts.json')
  		.then(function(data){
        return data.json()
  			// console.log(data.json());
  			// this.blogs = data.body.slice(0,10);
  			// console.log(this.blogs);
  		})
      .then(function(data){
        var blogsArray = [];
        for(let key in data){
          // console.log(key);
          // console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        // console.log(blogsArray);
        this.blogs = blogsArray;
        console.log(this.blogs);
      })
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) =>{
  			return blog.title.match(this.search);
  		})
  	}
  },
  filters:{
  	// "to-uppercase":function(value){
  	// 	return value.toUpperCase();
  	// }
  	toUppercase(value){
  		return value.toUpperCase();
  	}
  },
  directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
  			el.style.color = "#" + Math.random().toString(16).slice(2,8);
  		}
  	}
  }
}
</script>

<style>
.box-card,.search{
	margin-bottom: 20px;
}
a{
	text-decoration: none !important;
}

</style>



