<template>
	<el-card class="box-card clearfix" v-theme:position="'wide'">
		<el-row>
			<el-col :span="18" class="breadcrumb">
				<el-breadcrumb separator="/">
					<el-breadcrumb-item :to="{ path: '/' }">博客</el-breadcrumb-item>
					<el-breadcrumb-item>博客详情</el-breadcrumb-item>
				</el-breadcrumb>
			</el-col>
			<el-row type="flex" justify="end">
			<el-col :span="6">
				<router-link :to="'/'"><el-button type="primary" size="mini" icon="el-icon-back"></el-button></router-link> 
			</el-col>
			</el-row>
		</el-row>
	  <h2>{{blog.title}}</h2>
	  <hr>
	  <el-row type="flex" justify="start">
		  <el-col :span="12">
			  <el-tag size="mini" type="info">作者:{{blog.author}}</el-tag>
			  <el-tag size="mini">
				  <span v-for="category in blog.categories">
					  {{category}}
				  </span>
			  </el-tag>
		  </el-col>
	  </el-row>
	  <p class="text-regular">{{blog.content}}</p>
	  <el-row type="flex" justify="start">
	  	<el-col :span="12" :offset="20">
			<router-link :to="'/edit/' + id"><el-button type="primary" size="mini" icon="el-icon-edit"></el-button></router-link>
			<el-button type="danger" size="mini" icon="el-icon-delete" @click="deleteSingleBlog()"></el-button>
		</el-col>
		</el-row>	  
	</el-card>
</template>

<script>
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://myblog-30cdf.firebaseio.com/posts/' + this.id + ".json")
				.then(function(data){
					console.log(data);
					return data.json();
					// this.blog = data.body;
				})
				.then(function(data){
					this.blog = data;
				})
		},
		methods:{
			deleteSingleBlog(){
				this.$http.delete("https://myblog-30cdf.firebaseio.com/posts/" + this.id + ".json")
									.then(response =>{
										this.$router.push({path:'/'})
									})
			}
		}
	}
</script>
<style>
	.text-regular {
		padding: 20px;
		box-sizing: border-box;
		border: 1px solid #ebebeb;
		border-radius: 3px;
		font-size: 14px;
		line-height: 22px;
		color: #666;
		word-break: break-word;
		background-color: #fafafa;
	}
	.breadcrumb {
		margin-top: 5px;
	}
</style>


